import dayjs from 'dayjs'

export default function FilterStore(Vue) {
    // 10
    function Double(val) {
        return val < 10 ? '0' + val : val + ''
    }
    // 
    Vue.filter('number', function(data) {
        var str = data
        var s1 = str.substring(0, 6)
        var s2 = str.substring(str.length - 3, str.length)
        return s1 + '****' + s2
    })
     //  
     Vue.filter('orderNumber', function(data) {
        var str = data
        var s1 = str.substring(0, 8)
        var s2 = str.substring(str.length - 6, str.length)
        return s1 + '***' + s2
    })
    // 
    Vue.filter('fixed', function(data, line) {
        let money = data * 1
        return money.toFixed(line || 2)
    })
    // 
    Vue.filter('lotteryNumber', (data, length) => {
        // 202005220609
        if (!data) return '000'

        var len = data.length
        var str = data.substring(len - length, len)

        return str
    })
    // 
    Vue.filter('CountDown', (data, type) => {
        if (data >= 0) {
            var h = Double(Math.floor(data / 60 / 60))

            var m = Double(Math.floor(data / 60))

            var s = Double(data % 60)
            if(type){
                var time = ''.concat(m,'', s,'')
            }else{
                var time = ''.concat(h, ':', m, ':', s);
            }
            return time
        } else {
            return '00:00:00'
        }
    }),
    //  
    Vue.filter('zijin_type', data => {
        switch (data) {
            case -1:
                return ''
            case 2:
                return '/'
            case 3:
                return '/'
            case 9:
                return '/'
            case 11:
                return '/'
            case 12:
                return '/'
            case 13:
                return '/'
            case 14:
                return ''
            case 16:
                return '/()'
            case 17:
                return ''
            case 18:
                return ''
            case 19:
                return ''
            case 26:
                return '[][]'
            case 27:
                return '[][]'
            case 37:
                return '[FG][]'
            case 38:
                return '[][FG]'
            case 42:
                return '[][VG]'
            case 43:
                return '[VG][]'
            case 12:
                return '/'
            case 44:
                return ''
            case 45:
                return ''
            case 46:
                return ''
            case 47:
                return ''
            case 48:
                return ''
            case 49:
                return ''
            case 54:
                return ''
            case 55:
                return ''
            case 56:
                return '[][]'
            case 57:
                return '[][]'
            default:
                return data
        }
    })
    //  
    Vue.filter('tixian_type', data => {
        switch (data) {
            case 1:
                return ''
            default:
                return data
        }
    })
    //  
    Vue.filter('tixian_status', data => {
        switch (data) {
            case 0:
                return '...'
            case 1:
                return ''
            case 2:
                return ''
            default:
                return data
        }
    })
    //  
    Vue.filter('chongzhi_type', data => {
        switch (data) {
            case 1:
                return ''
            case 2:
                return ''
            case 3:
                return ''
            case 4:
                return ''
            case 5:
                return ''
            case 6:
                return 'QQ'
            case 7:
                return ''
            case 8:
                return ''
            case 9:
                return ''
            case 10:
                return ''
            case 11:
                return ''
            default:
                return data
        }
    })
    //  
    Vue.filter('chongzhi_status', data => {
        switch (data) {
            case 0:
                return ''
            case 1:
                return ''
            case 2:
                return ''
            default:
                return data
        }
    })
    //   
    Vue.filter('timer', data => {
        if(data){
            return dayjs(data).format('YYYY-MM-DD HH:mm:ss')
        }else{
            return '00:00:00'
        }
    })
    Vue.filter('timer1', data => {
        if(data){
            return dayjs(data).format('YYYY-MM-DD')
        }else{
            return '00:00:00'
        }
    })
}