<template>
    <div class="container back-one">
        <!-- <Header :back="true"></Header> -->
        <div class="wx-confirm-page">
            <div class="item">
                <span></span>
                <b>{{money}}</b>
                <var></var>
            </div>
            <div class="item1">
                <p><i>{{select.minMoney}}</i><i>{{select.maxMoney}}</i></p>
            </div>
            <div class="item">
                <span></span>
                <input maxlength="10" v-model="formData.userWeixinName" type="text" placeholder="">
            </div>
            <div class="btn">
                <button :class="{'active': formData.userWeixinName}" @click="submitFun"></button>
            </div>
            <div class="text">
                <p>""</p>
                <span>1""</span>
                <span>2"",</span>
                <span>3""</span>
                <span>4</span>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                formData: {
                    userWeixinName:''
                }
            }
        },
        created(){

        },
        computed: {
            ...mapState('Recharge', {
                user: state => state.user,
                money: state => state.money,
                select: state => state.select
            })
        },
        methods: {
            submitFun(){
                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '',
                });

                var data = {
                    ...this.formData,
                    ...this.user,
                    money: this.money,
                    weixinId: this.select.id,
                }
                this.$api.recharge.submitWeixinzz(data)
                .then(res=>{
                    setTimeout(() => {
                        this.$toast(res.description)
                        if(res.result==1){
                            this.$router.back(-1)
                        }
                    }, 500);
                })
            }
        }
    }
</script>

