<template>
    <div class="container back-one">

        <!-- <Header :back="true" title="">
            <span slot="right" @click="show2=true" class="jiao"></span>
        </Header> -->

        <div class="bank-card">
            <div class="tabs">
                <ul>
                    <div class="line" :style="{'left':`${tabs * 50}%`}">
                        <span></span>
                    </div>
                    <li @click="tabs=0" :class="{'acti': tabs==0}">
                        <span></span>
                    </li>
                    <li  @click="tabs=1" :class="{'acti': tabs==1}">
                        <span></span>
                    </li>
                </ul>
            </div>

            <div class="bank-info">
                <div class="wrap" :style="{'transform':`translateX(${-tabs * 7.5}rem)`}">
                    <div class="slide">
                        <div class="tips colors"><span>{{money}}</span></div>
                        <ul class="ul0" v-if="bankcardList">
                            <li>
                                <span></span>
                                <strong>{{bankcardList[select]['bankName']}}</strong>
                                <button @click="show1=true"></button>
                            </li>
                            <li>
                                <span></span>
                                <strong>{{bankcardList[select]['bankAccount']}}</strong>
                                <button type="button"
                                    v-clipboard:copy="bankcardList[select]['bankAccount']"
                                    v-clipboard:success="onCopy"
                                    ></button>
                            </li>
                            <li>
                                <span></span>
                                <strong>{{bankcardList[select]['userName']}}</strong>
                                <button type="button"
                                    v-clipboard:copy="bankcardList[select]['userName']"
                                    v-clipboard:success="onCopy"
                                    ></button>
                            </li>
                            <li>
                                <span></span>
                                <strong>{{bankcardList[select]['subBankName']}}</strong>
                                <button type="button"
                                    v-clipboard:copy="bankcardList[select]['subBankName']"
                                    v-clipboard:success="onCopy"
                                    ></button>
                            </li>
                        </ul>
                        <loadding v-else></loadding>


                        <div class="open-app">
                            <button @click="tabs = 1"></button>
                        </div>
  
                    </div>

                    <div class="slide">
                        <ul class="ul1">
                            <li>
                                <span></span>
                                <input class="red" type="text" readonly v-model="money">
                            </li>
                            <li>
                                <span></span>
                                <input type="text" @click="show=true" readonly v-model="formData.rkTime">
                            </li>
                            <li v-if="dyParam.dyParam">
                                <span>{{dyParam.dyParam}}</span>
                                <input v-model="formData.unParam" type="text" :placeholder="dyParam.dyParamTip">
                            </li>
                        </ul>
                        <div class="open-app">
                            <button @click="submitFun"></button>
                        </div>
                    </div>
                </div>
            </div>

            <div class="zhan-btn">
                <button @click="scroll"></button>
            </div>

                                  
            <div class="zhan-btn">
                <button @click="onOpen"></button>
            </div>

            <van-popup
                v-model="show"
                position="bottom"
                :style="{ height: '40%' }"
            >
            <van-datetime-picker
                v-model="currentDate"
                type="datetime"
                title=""
                :min-date="minDate"
                :max-date="maxDate"
                @cancel="show=false"
                @confirm="confirm"
                />
            </van-popup>

            <van-popup
                v-model="show1"
                position="bottom"
                :style="{ height: '50%' }"
            >
            <van-picker
                title=""
                show-toolbar
                :columns="columns"
                @confirm="onConfirm"
                @cancel="show1=false"
            />
            </van-popup>
        </div>

        <!--  -->
        <div class="alert-course" v-if="show2">
            <div class="layout">
                <h2><var @click="show2=false"></var></h2>
                <div class="box">
                    <div class="scroll mui-scroll">
                        <p><i>1</i>+</p>
                        <img style="width:60%;" src="../../static/img/wx/wxyhk_1.png" alt="">
                        <p><i>2</i></p>
                        <img src="../../static/img/wx/wxyhk_2.png" alt="">
                        <p><i>3</i></p>
                        <img src="../../static/img/wx/wxyhk_3.png" alt="">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    import { dataToString, getDay } from "@/tool/common/time.js";
    export default {
        data(){
            return {
                tabs: 0,

                select:0,
                bankcardList: false,

                show:false,

                show1: false,

                show2: false,

                minDate: new Date(2020, 0, 1),
                maxDate: new Date(2025, 10, 1),
                currentDate: new Date(),

                formData:{
                    rkTime:'',
                    unParam:'',
                },
                dyParam:{

                }
            }
        },
        created(){
            this.getNetData();
            this.formData.rkTime = getDay();
            this.getDyParam()
        },
        computed:{
            ...mapState('Recharge',{
                user: state => state.user,
                money: state => state.money
            }),
            columns(){
                var arr = []
                if(this.bankcardList){
                    this.bankcardList.forEach(item=>{
                        arr.push(`${item.bankName}-${item.bankAccount}`)
                    })
                }
                return arr
            }
        },
        methods: {
            scroll(){
                this.show2 = true
                this.$nextTick(()=>{
                    this.mui(".alert-course .layout .box").scroll({
                        scrollY: true, //
                        scrollX: false, //
                        startX: 0, //x
                        startY: 0, //y
                        indicators: true, //
                        deceleration: 0.0006, //,
                        bounce: true //
                    })
                })
            },
            nextPage(){
                this.$router.push('/bankCard/courseWx')
            },
            submitFun(){

                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '',
                });

                console.log(this.bankcardList);
                
                var data = {
                    ...this.formData,
                    ...this.user,
                    money: this.money,
                    bankCardId: this.bankcardList[this.select]['id'],
                }
                this.$api.recharge.submitWeChat2Bank(data)
                .then(res=>{
                    console.log(res);
                    setTimeout(() => {
                        if(res.result==1){
                            this.$toast({message:`\r\n3-10`, duration: 3000,})
                            this.$router.back(-1)
                        }else{
                            this.$toast(res.description)
                        }
                    }, 200);
                })
            },
            confirm(date){
                this.show = false
                this.formData.rkTime = dataToString(date)
            },
            onConfirm(value, index){
                this.select = index
                this.show1 = false
            },
            onOpen(){
                this.$dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    this.tabs = 1
                    if(this.env.app){
                        var json = 'wx'
                        if(this.env.isAndroid){
                            window.android.openChat(json);
                        }
                        if(this.env.isIOS){
                            window.webkit.messageHandlers.openChat.postMessage(json);
                        }
                    }else{
                        window.open('weixin://')
                    }
                });
            },
            onCopy(){
                this.$toast('')
            },
            // 
            getNetData(){
                this.$api.recharge.getSystemBankCard(this.user)
                .then(res=>{
                    this.bankcardList = res.bankcardList
                })
            },
            getDyParam(){
                this.$api.recharge.getDyParam(this.user)
                .then(res=>{
                    console.log(res);
                    if(res.result){
                        this.dyParam = res
                    }
                })
            }
        },

    }
</script>

<style>

</style>