import dayjs from 'dayjs'
import filter from "./filter";
import assist from './assist.js'

import { mapState } from "vuex";

let plug = {}

plug.install = function(Vue, options) {
    // 
    Vue.directive('number', {
        inserted: function(el, binding, vnode) {
            el.focus()
        },
    })

    // 
    Vue.mixin({
        data(){
            return {
                env: {}
            }
        },
        watch:{
            beOverdue(user){
                this.$store.commit('User/SET_USER', user);
            }
        },
        created() {
            var env = assist.getUserAgent()
            this.env = env
            if (env.build == 'APP') {
                this.env.app = true
            } else {
                this.env.app = false
            }
        },
        computed: {
            ...mapState('User', {
                baseUser: state => state.baseUser,
                beOverdue: state => state.beOverdue,
            }),
            ...mapState('Game', {
                $me: state => state.getUserBaseInfo,
            })
        },
        methods: {
            // 
            callHandle(json) {
                if (this.env.isIOS) {
                    window.webkit.messageHandlers.NativeModel.postMessage(json);
                }
                if (this.env.isAndroid) {
                    window.android.NativeModel(json);
                }
            },
            // 
            bankPicture(name) {
                console.log(name);
                switch (name) {
                    case '':
                        return '4.png'
                    case '':
                        return '2.png'
                    case '':
                        return '5.png'
                    case '':
                        return '6.png'
                    case '':
                        return '7.png'
                    case '':
                        return '8.png'
                    case '':
                        return '9.png'
                    case '':
                        return '3.png'
                    case '':
                        return '10.png'
                    case '':
                        return '11.png'
                    case '':
                        return '12.png'
                    case '':
                        return '13.png'
                    case '':
                        return '15.png'
                    case '':
                        return '16.png'
                    case '':
                        return '17.png'
                    case '':
                        return '18.png'
                    case '':
                        return '19.png'
                    case '':
                        return '1.png'
                    case '':
                        return '20.png'
                    default:
                        return '14.png'
                }
                
            },
            // mui-scroll 
            mui_scroll(ele) {
                setTimeout(() => {
                    this.mui(ele).scroll({
                        scrollY: true, //
                        scrollX: false, //
                        startX: 0, //x
                        startY: 0, //y
                        indicators: true, //
                        deceleration: 0.0006, //,
                        bounce: true //
                    })
                }, 200);
            }
        }
    })

    // 
    filter(Vue)

    Vue.filter('Date', data => dayjs(data).format('YYYY-MM-DD HH:mm:ss'))

    Vue.prototype.$user = function(methodOptions) {
       console.log('mixins $user function');
       return this.$route.query
    }
    Vue.prototype.$assist = assist
}

export default plug